.partner {
  margin-bottom: 155px;

  .subtitle {
    margin: 0;
    font-weight: bold;
    font-size: 28px;
    line-height: 149.6%;
    margin-bottom: 44px;
  }

  .customers {
    display: flex;
    width: 100%;
    border: 1px solid rgba(0, 0, 0, 0.1);
    background-color: rgba(0, 0, 0, 0.1);

    .container {
      @include center;
      min-height: 210px;
      width: 25%;
      background-color: $color_white;

      &:not(:last-child) {
        border-right: 1px solid rgba(0, 0, 0, 0.1);
      }
    }

    &__slider {

      &_container {
        display: flex;
        width: 100%;
      }
    }
  }
}

@media (max-width: $desktop) {
  .partner {
    margin-bottom: 90px;

    .subtitle {
      font-size: 26px;
      margin-bottom: 30px;
    }

    .customers {

      .container {
        min-height: 170px;

        &:nth-child(1) {
          .image {
            width: 178px;
            height: 44px;
          }
        }

        &:nth-child(2) {
          .image {
            width: 170px;
            height: 32px;
          }
        }

        &:nth-child(3) {
          .image {
            width: 196px;
            height: 50px;
          }
        }

        &:nth-child(4) {
          .image {
            width: 142px;
            height: 36px;
          }
        }
      }
    }
  }
}

@media (max-width: $tablet) {
  .partner {
    overflow: hidden;

    .wrapper {
      padding: 0 20px 0;
    }

    .subtitle {
      font-size: 20px;
      margin-bottom: 30px;
    }

    .customers {
      border: none;
      background-color: transparent;

      .container {
        min-height: 132px;

        &:nth-child(1) {
          .image {
            width: 138px;
            height: 34px;
          }
        }

        &:nth-child(2) {
          .image {
            width: 132px;
            height: 24px;
          }
        }

        &:nth-child(3) {
          .image {
            width: 153px;
            height: 38px;
          }
        }

        &:nth-child(4) {
          .image {
            width: 110px;
            height: 28px;
          }
        }
      }

      &__slider {

        .flex-viewport {
          overflow: visible !important;
          padding-left: 0;
          width: 228px !important;
          height: 132px !important;
        }

        &_item {
          display: flex !important;
          width: 228px !important;
          border: 1px solid rgba(0, 0, 0, 0.1);
          margin-right: -1px;
        }
      }
    }
  }
}

@media (max-width: $mobile) {
  .partner {
    margin-bottom: 55px;

    .wrapper {
      padding: 0 10px 0 14px;
    }

    .subtitle {
      font-size: 16px;
    }

    .customers {
      border: none;
      background-color: transparent;

      .container {
        min-height: 85px;

        &:nth-child(1) {
          .image {
            width: 88px;
            height: 22px;
          }
        }

        &:nth-child(2) {
          .image {
            width: 85px;
            height: 16px;
          }
        }

        &:nth-child(3) {
          .image {
            width: 105px;
            height: 24px;
          }
        }

        &:nth-child(4) {
          .image {
            width: 80px;
            height: 20px;
          }
        }
      }

      &__slider {

        .flex-viewport {
          overflow: visible !important;
          padding-left: 0;
          width: 160px !important;
          height: 89px !important;
        }

        &_item {
          display: flex !important;
          width: 160px !important;
          border: 1px solid rgba(0, 0, 0, 0.1);
          margin-right: -1px;
        }
      }
    }
  }
}
