.landing {
  position: relative;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  margin-top: -77px;

  &::before {
    z-index: 15;
    content: '';
    position: absolute;
    width: 36px;
    height: 36px;
    bottom: 70px;
    left: 70px;
    background-image: url("data:image/svg+xml,%3Csvg width='36' height='36' viewBox='0 0 36 36' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg opacity='0.5'%3E%3Ccircle cx='1.54545' cy='1.54545' r='1.54545' fill='white'/%3E%3Ccircle cx='1.54545' cy='34' r='1.54545' fill='white'/%3E%3Ccircle cx='34' cy='1.54545' r='1.54545' fill='white'/%3E%3Ccircle cx='34' cy='34' r='1.54545' fill='white'/%3E%3C/g%3E%3C/svg%3E");
  }

  .media {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    overflow: hidden;
    background: #090909 url('../img/landing_bg_desk.png') no-repeat center center;
    background-size: cover;
  }

  .video {
    position: absolute;
    top: 50%;
    left: 50%;
    min-width: 100%;
    min-height: 100%;
    object-fit: cover;
    transform: translate(-50%, -50%);
  }

  .wrapper {
    z-index: 10;
    height: 100%;
    padding-top: 155px;
    color: $color_white;
  }

  .headline {
    position: relative;
    @include font_Furore;
    font-size: 139px;
    color: transparent;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: rgba(255, 255, 255, 0.3);
    margin-bottom: 105px;


    &::before {
      content: '';
      position: absolute;
      width: 68px;
      height: 68px;
      top: 36px;
      right: -155px;
      background-image: url("data:image/svg+xml,%3Csvg width='68' height='68' viewBox='0 0 68 68' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg opacity='0.5'%3E%3Ccircle cx='1.54545' cy='1.54545' r='1.54545' fill='white'/%3E%3Ccircle cx='1.54545' cy='34' r='1.54545' fill='white'/%3E%3Ccircle cx='1.54545' cy='66.4545' r='1.54545' fill='white'/%3E%3Ccircle cx='34' cy='1.54545' r='1.54545' fill='white'/%3E%3Ccircle cx='66.4546' cy='1.54545' r='1.54545' fill='white'/%3E%3Ccircle cx='34' cy='34' r='1.54545' fill='white'/%3E%3Ccircle cx='34' cy='66.4545' r='1.54545' fill='white'/%3E%3Ccircle cx='66.4546' cy='34' r='1.54545' fill='white'/%3E%3Ccircle cx='66.4546' cy='66.4545' r='1.54545' fill='white'/%3E%3C/g%3E%3C/svg%3E");
    }
  }

  .content {
    width: 100%;
    justify-content: space-between;
    overflow: hidden;
  }

  .container {
    flex-direction: column;
  }

  .title {
    font-size: 54px;
    margin-bottom: 35px;
    letter-spacing: 0.05em;
  }

  .text {
    font-size: 18px;
    margin-bottom: 95px;
  }

  .list {
    flex-direction: column;
  }

  .item {
    display: flex;
    justify-content: space-between;
    align-items: center;

    &:not(:last-child) {
      margin-bottom: 50px;
    }

    .theses {
      width: 310px;
      font-size: 14px;
      line-height: 182.19%;
      margin-top: 10px;
    }
  }

  .number {
    @include center;
    @include font_Furore;
    min-width: 60px;
    margin-right: 30px;
    font-size: 90px;
    line-height: 100%;
    color: $color_blue;
  }

  .link {
    color: $color_blue;
  }

  .button {
    align-self: flex-start;
    margin-bottom: 170px;
  }
}

@media (max-width: $desktop) {
  .landing {
    margin-top: -72px;

    .wrapper {
      padding-top: 122px;
    }

    .headline {
      font-size: 107px;
      margin-bottom: 92px;
    }

    .title {
      font-size: 36px;
      margin-bottom: 50px;
    }

    .number {
      font-size: 80px;
    }

    .item {
      &:not(:last-child) {
        margin-bottom: 33px;
      }
    }
  }
}

@media (max-width: $tablet) {
  .landing {
    margin-top: -57px;

    &::before {
      display: none;
    }

    .wrapper {
      padding: 65px 20px 0;
    }

    .headline {
      font-size: 70px;
      margin-bottom: 27px;

      &::before {
        display: none;
      }
    }

    .container {
      max-width: 50%;
    }

    .title {
      font-size: 26px;
      margin-bottom: 25px;
    }

    .text {
      font-size: 12px;
      margin-bottom: 50px;
    }

    .number {
      font-size: 76px;
      line-height: 76px;
      margin-right: 10px;
    }

    .list {
      margin-top: -10px;
    }

    .item {
      &:not(:last-child) {
        margin-bottom: 15px;
      }

      .theses {
        width: 305px;
        font-size: 12px;
        line-height: 166.69%;
      }
    }
  }
}

@media (max-width: $mobile) {
  .landing {
    margin-top: -52px;

    .wrapper {
      padding: 65px 0 0 14px;
    }

    .headline {
      font-size: 54px;
      margin-bottom: 27px;
      overflow: hidden;
      width: 100%;
    }

    .content {
      flex-direction: column;
    }

    .container {
      max-width: 100%;
    }

    .list {
      margin-bottom: 45px;
    }

    .item {
      flex-direction: column;
      align-items: flex-start;
    }

    .title {
      max-width: 300px;
      font-size: 22px;
      margin-bottom: 18px;

      .theses {
        margin-top: 0;
      }
    }

    .text {
      font-size: 12px;
      margin-bottom: 30px;
    }

    .number {
      font-size: 34px;
      line-height: 136%;
      margin-right: 10px;
      text-align: left;
      justify-content: flex-start;
    }

    .button {
      margin-bottom: 45px;
    }
  }
}
