.values {
  padding-top: 130px;
  padding-bottom: 550px;
  background: #030b16 url('../img/values_bg_desk.png') no-repeat bottom center;
  background-size: cover;

  .title {
    color: $color_blue;
  }

  &__title {
    margin-bottom: 17px;
  }

  .list {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    margin-bottom: 130px;
  }

  .item {
    display: flex;
    flex-direction: column;
    width: 26%;
    min-height: 185px;

    &:nth-child(1) {
    margin-bottom: 45px;
    }

    &:nth-child(2) {
      margin: 0 10.5% 0;
      margin-bottom: 45px;
    }

    &:nth-child(3) {
      margin-bottom: 45px;
    }

    &:nth-child(5) {
      margin: 0 10.5% 0;
    }
  }

  .number {
    @include font_Furore;
    font-size: 50px;
    letter-spacing: 0.05em;
    color: transparent;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: rgba(255, 255, 255, 0.3);
    margin-bottom: 15px;
  }

  .subtitle {
    font-weight: bold;
    font-size: 16px;
    line-height: 197.69%;
    color: $color_blue;
    margin-bottom: 12px;
  }

  .text {
    font-size: 14px;
    line-height: 166.69%;
    color: $color_white;
  }
}

.decisions {

  &__title {
    margin-bottom: 60px;
  }

  .hugging_link {
    display: flex;
    flex-shrink: 0;
    width: 100%;
    height: 100%;
    color: $color_white;
  }

  &__flipping {
    @include center;
    position: absolute;
    top: -50px;
    right: 0;
  }

  &__slider {
    position: relative;
    width: 100%;
    min-width: 0;
    flex-shrink: 0;

    .flex-viewport {
      min-height: 495px !important;
      padding-left: 0;
      padding-right: calc((100% - 474px)/2);
      flex-shrink: 0;
    }

    &_container {
      width: 100% !important;
      align-items: center;
      flex-shrink: 0;
    }

    &_item {
      display: flex !important;
      flex-direction: column;
      flex-shrink: 0;
      width: 450px;
      margin: 0 12px 0;
      color: $color_white;

      .button {
        align-self: flex-start;
      }
    }

    &_image {
      width: 100%;
      margin-bottom: 20px;
    }

    &_text {
      font-weight: bold;
      font-size: 22px;
      line-height: 142.6%;
      opacity: 0.9;
      margin-bottom: 20px;
      color: $color_white;
    }

    .flex-direction-nav a.flex-prev {
      background: #212A37 url("data:image/svg+xml,%3Csvg width='10' height='17' viewBox='0 0 10 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M8.48528 0L0 8.48528L3.95775e-05 8.48532L8.58307e-06 8.48535L8.48529 16.9706L9.8995 15.5564L2.8284 8.48531L9.8995 1.41421L8.48528 0Z' fill='white'/%3E%3C/svg%3E%0A");
      background-repeat: no-repeat;
      background-position: center;
      background-size: 10px 17px;

      &:hover {
        background-color: #3A4657;
        background-image: url("data:image/svg+xml,%3Csvg width='10' height='17' viewBox='0 0 10 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M8.48528 0L0 8.48528L3.95775e-05 8.48532L8.58307e-06 8.48535L8.48529 16.9706L9.8995 15.5564L2.8284 8.48531L9.8995 1.41421L8.48528 0Z' fill='%232482FF'/%3E%3C/svg%3E%0A");
      }
    }

    .flex-direction-nav a.flex-next {
      background: #212A37 url("data:image/svg+xml,%3Csvg width='10' height='17' viewBox='0 0 10 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M1.51472 0L10 8.48528L9.99996 8.48532L9.99999 8.48535L1.51471 16.9706L0.100496 15.5564L7.1716 8.48531L0.100505 1.41421L1.51472 0Z' fill='white'/%3E%3C/svg%3E%0A");
      background-repeat: no-repeat;
      background-position: center;
      background-size: 10px 17px;

      &:hover {
        background-color: #3A4657;
        background-image: url("data:image/svg+xml,%3Csvg width='10' height='17' viewBox='0 0 10 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M1.51472 0L10 8.48528L9.99996 8.48532L9.99999 8.48535L1.51471 16.9706L0.100496 15.5564L7.1716 8.48531L0.100505 1.41421L1.51472 0Z' fill='%232482FF'/%3E%3C/svg%3E%0A");
      }
    }
  }
}

@media (max-width: $desktop) {
  .values {
    padding-top: 90px;
    padding-bottom: 450px;

    &__title {
      margin-bottom: 47px;
    }

    .list {
      margin-bottom: 90px;
    }

    .item {
      width: 32%;

      &:nth-child(1) {
      margin-bottom: 45px;
      }

      &:nth-child(2) {
        margin: 0 2% 0;
        margin-bottom: 45px;
      }

      &:nth-child(3) {
        margin-bottom: 45px;
      }

      &:nth-child(5) {
        margin: 0 2% 0;
      }
    }

    .decisions {

      &__title {
        margin-bottom: 50px;
      }

      &__slider {

        .flex-viewport {
          min-height: 440px !important;
          padding-right: calc((100% - 384px)/2);
          flex-shrink: 0;
        }

        &_item {
          width: 360px;
        }
      }
    }
  }
}

@media (max-width: $tablet) {
  .values {
    padding-top: 74px;
    padding-bottom: 480px;
    overflow: hidden;

    &__title {
      margin-bottom: 30px;
    }

    .wrapper {
      padding: 0 20px 0;
    }

    .list {
      margin-bottom: 15px;
    }

    .item {
      width: 47%;

      &:nth-child(1) {
      margin-bottom: 0;
      margin-right: auto;
      }

      &:nth-child(2) {
        margin: 0 0 0;
        margin-bottom: 0;
      }

      &:nth-child(3) {
        margin-bottom: 0;
        margin-right: auto;
      }

      &:nth-child(5) {
        margin: 0 0 0;
      }
    }

    .number {
      font-size: 40px;
      margin-bottom: 7px;
    }

    .subtitle {
      font-size: 14px;
      margin-bottom: 5px;
    }

    .text {
      font-size: 12px;
    }

    .decisions {

      &__title {
        margin-bottom: 40px;
      }

      &__slider {

        .flex-viewport {
          min-height: 354px !important;
          flex-shrink: 0;
          overflow: visible !important;
        }

        &_item {
          width: 285px;
        }

        &_image {
          margin-bottom: 18px;
        }

        &_text {
          font-size: 18px;
        }

        .flex-control-nav {
          bottom: -50px;
        }

        .flex-control-paging li a {
          width: 10px;
          height: 10px;
          opacity: 0.1;
          background-color: rgba(255, 255, 255, 0.9);

          &.flex-active {
            opacity: 1;
            background-color: $color_blue;
          }
        }
      }
    }
  }
}


@media (max-width: $mobile) {
  .values {
    padding-top: 50px;
    padding-bottom: 585px;

    .wrapper {
      padding: 0 10px 0 14px;
    }

    .list {
      margin-bottom: 40px;
    }

    .item {
      width: 100%;
      min-height: 140px;

      &:nth-child(1) {
      margin-bottom: 22px;
      }

      &:nth-child(2) {
        margin-bottom: 22px;
      }

      &:nth-child(3) {
        margin-bottom: 22px;
      }

      &:nth-child(4) {
        margin-bottom: 22px;
      }
    }

    .decisions__title {
      margin-bottom: 30px;
    }

    .number {
      margin-bottom: 3px;
    }

    .subtitle {
      margin-bottom: 2px;
    }

    .text {
      font-size: 12px;
    }

    .decisions {

      &__title {
        margin-bottom: 30px;
      }

      &__slider {

        .flex-viewport {
          min-height: 331px !important;
          width: 276px !important;
          padding-left: 0;
          padding-right: 0;
        }

        &_item {
          width: 276px !important;
          margin: 0 0 0;

          &:not(:first-child) {
            margin: 0 5px 0;
          }

          &:first-child {
            margin-right: 5px;
          }
        }

        &_image {
          margin-bottom: 15px;
        }

        &_text {
          font-size: 16px;
          margin-bottom: 25px;
        }

        .button_bg_blue {
          width: 100%;
        }

        .flex-control-nav {
          bottom: -50px;
        }
      }
    }
  }
}
