.header {
  position: sticky;
  z-index: 90;
  top: 0;
  background-color: transparent;
  border-bottom: 1px solid rgba(255, 255, 255, 0.15);

  &__shadow {
    background-color: #030B16;
  }

  .wrapper {
    min-height: 75px;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  .menu {
    color: $color_white;

    .item {
      @include center;
      min-height: 42px;
      margin-right: 40px;

      &:first-child {
        margin-left: 40px;
      }
    }

    .inpopup {
      display: none;
    }

    .link {
      font-size: 14px;
      color: $color_white;
      transition: .5s;

      &:hover {
        color: $color_blue;
      }
    }
  }

  .open_menu {
    display: none;
  }
}

@media (max-width: $desktop) {
  .header {

    .wrapper {
      min-height: 70px;
    }

    .menu {
      .item {
        margin-right: 25px;
      }

      .link {
        font-size: 13px;
      }
    }
  }
}

@media (max-width: $tablet) {
  .header {
    position: relative;

    .wrapper {
      min-height: 55px;
    }

    .logo {
      margin-left: 20px;
    }

    .open_menu {
      display: flex;
      padding-left: 17px;
      color: $color_white;
      font-weight: 600;
      font-size: 12px;
      line-height: 15px;
      text-transform: uppercase;
      letter-spacing: 0.11em;
      background-image: url("data:image/svg+xml,%3Csvg width='11' height='11' viewBox='0 0 11 11' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='1.20455' cy='1.20455' r='1.20455' fill='white'/%3E%3Ccircle cx='1.20455' cy='9.20455' r='1.20455' fill='white'/%3E%3Ccircle cx='9.20455' cy='1.20455' r='1.20455' fill='white'/%3E%3Ccircle cx='9.20455' cy='9.20455' r='1.20455' fill='white'/%3E%3C/svg%3E%0A");
      background-repeat: no-repeat;
      background-position: left center;
    }

