      background-repeat: no-repeat;
      background-position: 75% 0;
      background-size: auto 85%;

      .list {
        flex-grow: 1;
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-around;
        padding: 75px 60px 0;
      }

      .inpopup {
        display: flex;
      }

      .item {
        margin-right: 0;

        &:first-child {
          margin-left: 0;
          margin-bottom: 40px;
        }
        &:last-child {
          margin-left: 0;
          margin-top: 40px;
          margin-bottom: 40px;
        }
      }

      .close {
        z-index: 105;
        position: absolute;
        top: 25px;
        right: 25px;
        width: 16px;
        height: 16px;
        background-image: url("data:image/svg+xml,%3Csvg  viewBox='0 0 16 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath opacity='0.7' fill-rule='evenodd' clip-rule='evenodd' d='M8.48528 8.49998L15.5563 1.42891L14.8492 0.721802L7.77817 7.79287L0.707194 0.72189L8.53616e-05 1.429L7.07107 8.49998L0 15.571L0.707109 16.2782L7.77817 9.20709L14.8493 16.2782L15.5564 15.5711L8.48528 8.49998Z' fill='white'/%3E%3C/svg%3E");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      }

      .link,
      .tel {
        color: $color_white;
        font-weight: bold;
        font-size: 16px;
        line-height: 20px;
        text-transform: uppercase;
      }

      .button {
        min-height: 55px;
        padding: 0 30px 0;
        font-weight: bold;
        font-size: 16px;
        text-align: center;
        text-transform: uppercase;
        color: $color_white;
        background-color: $color_blue;
        text-transform: none;
        border: 1px solid rgba(255, 255, 255, 0.5);
        border-left-color: rgba(255, 255, 255, 1);
        box-shadow: -2px 0px 0px 0px rgba(255, 255, 255, 1);

        &:hover,
        &:focus {
          background-color: #3e91ff;
          border-color: rgba(255, 255, 255, 0);
          border-right-color: rgba(255, 255, 255, 1);
          box-shadow: 2px 0px 0px 0px rgba(255, 255, 255, 1);
          outline: none;
        }

        &:active {
          background-color: $color_blue;
          border: 1px solid rgba(255, 255, 255, 0.5);
          border-left-color: rgba(255, 255, 255, 1);
          box-shadow: -2px 0px 0px 0px rgba(255, 255, 255, 1);
        }

        &:disabled {
          opacity: 0.5;
        }
      }
    }

    .basement {
      min-height: 70px;
      width: 100%;
      padding: 0 30px;
      align-self: flex-end;
      align-items: center;
      justify-content: space-between;
      border-top: 1px solid rgba(255, 255, 255, 0.15);

      .copyright,
      .policy {
        @include center;
        font-weight: 500;
        font-size: 12px;
        text-decoration-line: underline;
        color: rgba(255, 255, 255, 0.5);
      }

      .policy {
        transition: .5s;

        &:hover {
          color: $color_blue;
        }

      }

      .dev_log {
        @include center;
        width: 14px;
        height: 22px;
        background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 31 50' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg opacity='0.5' clip-path='url(%23clip0)'%3E%3Cpath opacity='0.48' d='M0 10.9602L31 29.3318V38.2045L0 19.8329V10.9602Z' fill='white'/%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M15.5 7.62004L0 16.7015V9.08142L15.5 0L31 8.97704V16.5971L15.5 7.62004ZM15.5 42.2754L31 33.194V40.9184L15.5 49.9998L0 40.9184V33.2984L15.5 42.2754ZM15.6041 15.9705L0.312107 24.9475L15.5 33.8202L30.896 24.8431L15.6041 15.9705Z' fill='white'/%3E%3C/g%3E%3Cdefs%3E%3CclipPath id='clip0'%3E%3Crect width='31' height='50' fill='white'/%3E%3C/clipPath%3E%3C/defs%3E%3C/svg%3E%0A");
        background-repeat: no-repeat;
        background-position: center;
      }
    }

    .communication {
      display: none;
    }
  }
}

@media (max-width: $mobile) {
  .header {

    .wrapper {
      min-height: 50px;
    }

    .menu {

      .list {
        padding: 70px 30px 0;
      }

      .item {

        &:first-child {
          margin-bottom: 10px;
        }
        &:last-child {
          margin-top: 40px;
          margin-bottom: 40px;
        }
      }

      .link {
        font-size: 14px;
      }

      .button {
        min-height: 38px;
        font-size: 12px;
      }
    }

    .basement {
      flex-wrap: wrap;
      justify-content: flex-start;

      .dev_log {
        margin: 0 15px;
      }
    }
  }
}
