.technologies {
  overflow: hidden;
  padding-top: 115px;
  background-color: $color_blue;
  color: $color_white;

  .title {
    margin-bottom: 70px;
  }

  .list {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    margin-bottom: 75px;
  }

  .item {
    display: flex;
    flex-direction: column;
    width: 33.3333%;
    min-height: 345px;
    padding: 70px 20px 40px 40px;
    background: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.1);

    &:nth-child(2) {
      border-left-width: 0;
      border-right-width: 0;
    }

    &:nth-child(4) {
      border-top-width: 0;
    }

    &:nth-child(5) {
      border-right-width: 0;
      border-left-width: 0;
      border-top-width: 0;
    }

    &:nth-child(6) {
      border-top-width: 0;
    }
  }

  .container {
    display: flex;
    align-items: center;
    min-height: 85px;
    margin-bottom: 22px;
  }

  .subtitle {
    font-weight: bold;
    font-size: 16px;
    margin-bottom: 18px;
  }

  .text {
    display: flex;
    max-width: 390px;
    font-size: 14px;
    line-height: 167%;
  }

  .caption {
    @include font_Furore;
    display: flex;
    flex-direction: column;
    font-size: 80px;
    line-height: 136.6%;
    letter-spacing: 0.05em;
  }

  .line {
    white-space: nowrap;

    &:first-child {
      margin-left: -390px;
      margin-bottom: 20px;
    }

    &:nth-child(2) {
      margin-left: -60px;
      margin-bottom: 20px;
    }

    &:last-child {
      margin-left: -10px;
      margin-bottom: 95px;
    }
  }

  .stroke {
    color: transparent;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: rgba(255, 255, 255, 0.3);
    margin: 0 40px 0;
  }
}

@media (max-width: $desktop) {
  .technologies {
    padding-top: 88px;

    .title {
      margin-bottom: 65px;
    }

    .list {
      margin-bottom: 65px;
    }

    .item {
      width: 50%;
      padding: 60px 20px 40px 50px;

      &:nth-child(2) {
        border-left-width: 0;
        border-right-width: 1px;
      }

      &:nth-child(3) {
        border-top-width: 0;
      }

      &:nth-child(4) {
        border-top-width: 0;
        border-left-width: 0;
      }

      &:nth-child(5) {
        border-right-width: 1px;
        border-left-width: 1px;
        border-top-width: 0;
      }

      &:nth-child(6) {
        border-top-width: 0;
        border-left-width: 0;
      }
    }

    .container {
      margin-bottom: 30px;
    }

    .text {
      max-width: 495px;
    }

    .caption {
      font-size: 55px;
    }

    .line {

      &:first-child {
        margin-bottom: 15px;
      }

      &:nth-child(2) {
        margin-bottom: 15px;
      }

      &:last-child {
        margin-bottom: 90px;
      }
    }
  }
}

@media (max-width: $tablet) {
  .technologies {
    padding-top: 74px;

    .wrapper {
      padding: 0 20px 0;
    }

    .container {
      margin-bottom: 10px;
    }

    .title {
      margin-bottom: 45px;
    }

    .list {
      margin-bottom: 55px;
    }

    .item {
      min-height: 285px;
      padding: 25px 20px 20px 25px;


      &:nth-child(1) {
        .icon {
          width: 66px;
          height: 66px;
        }
      }

      &:nth-child(2) {
        .icon {
          width: 55px;
          height: 55px;
        }
      }

      &:nth-child(3) {
        .icon {
          width: 57px;
          height: 57px;
        }
      }

      &:nth-child(4) {
        .icon {
          width: 57px;
          height: 57px;
        }
      }

      &:nth-child(5) {
        .icon {
          width: 63px;
          height: 63px;
        }
      }

      &:nth-child(6) {
        .icon {
          width: 49px;
          height: 49px;
        }
      }
    }

    .subtitle {
      font-size: 14px;
    }

    .text {
      max-width: 315px;
      font-size: 12px;
    }

    .caption {
      font-size: 36px;
    }

    .line {

      &:first-child {
        margin-bottom: 10px;
      }

      &:nth-child(2) {
        margin-bottom: 10px;
      }

      &:last-child {
        margin-bottom: 70px;
      }
    }
  }
}

@media (max-width: $mobile) {
  .technologies {
    padding-top: 50px;

    .wrapper {
      padding: 0 10px 0 14px;
    }

    .title {
      margin-bottom: 35px;
    }

    .container {
      margin-bottom: 0px;
    }

    .list {
      margin-bottom: 33px;
    }

    .item {
      min-height: 270px;
      width: 100%;
      padding: 15px 23px 20px 23px;


      &:nth-child(1) {
        border-top-width: 1px;
        border-bottom-width: 0;
        border-left-width: 1px;
        border-right-width: 1px;

        .icon {
          width: 52px;
          height: 44px;
        }
      }

      &:nth-child(2) {
        border-top-width: 1px;
        border-bottom-width: 0;
        border-left-width: 1px;
        border-right-width: 1px;

        .icon {
          width: 48px;
          height: 48px;
        }
      }

      &:nth-child(3) {
        border-top-width: 1px;
        border-bottom-width: 0;
        border-left-width: 1px;
        border-right-width: 1px;

        .icon {
          width: 49px;
          height: 49px;
        }
      }

      &:nth-child(4) {
        border-top-width: 1px;
        border-bottom-width: 0;
        border-left-width: 1px;
        border-right-width: 1px;

        .icon {
          width: 49px;
          height: 49px;
        }
      }

      &:nth-child(5) {
        border-top-width: 1px;
        border-bottom-width: 0;
        border-left-width: 1px;
        border-right-width: 1px;

        .icon {
          width: 51px;
          height: 51px;
        }
      }

      &:nth-child(6) {
        border-top-width: 1px;
        border-bottom-width: 1px;
        border-left-width: 1px;
        border-right-width: 1px;

        .icon {
          width: 41px;
          height: 41px;
        }
      }
    }

    .caption {
      font-size: 19px;
    }

    .line {

      &:first-child {
        margin-bottom: 7px;
      }

      &:nth-child(2) {
        margin-bottom: 6px;
      }

      &:last-child {
        margin-bottom: 45px;
      }
    }
  }
}
