.services {
  padding-top: 100px;
  margin-bottom: 140px;

  .title {
    color: $color_black;
    margin-bottom: 83px;
  }

  .content {
    margin-bottom: 60px;
  }

  .navigation {
    margin-right: 70px;
  }

  .services__flipping {
    display: none;
  }

  .list {
    flex-direction: column;
  }

  .item {
    @include font_Roboto;
    display: flex;
    min-width: 328px;
    min-height: 105px;
    margin-bottom: 5px;
    font-size: 16px;
    line-height: 140%;
    color: $color_gray;
    cursor: pointer;

    .number {
      @include center;
      min-width: 37px;
      border: 1px solid rgba(0, 0, 0, 0.1);
      transition: .5s;

      .rotate {
        transform: rotate(-90deg);
      }
    }

    .text {
      display: flex;
      align-items: center;
      width: 100%;
      border: 1px solid rgba(0, 0, 0, 0.1);
      border-left: none;
      padding: 0 24px 0;
      transition: .5s;
    }

    &:hover,
    &:focus {
      color: $color_blue;

      .number,
      .text {
        border-color: $color_blue;
      }
    }

    &.active {
      .number {
        background-color: $color_blue;
        color: $color_white;
      }
    }
  }

  .block {
    display: flex;
    flex-direction: column;
    color: $color_black;

    .subtitle {
      font-weight: bold;
      font-size: 28px;
      margin-bottom: 28px;
    }

    .image {
      width: 100%;
      margin-bottom: 32px;
    }

    .text {
      line-height: 160%;
      margin-bottom: 52px;
    }

    .button {
      align-self: flex-start;
    }
  }

  .banner {
    display: flex;
    justify-content: flex-end;
    width: 100%;
    padding: 60px 90px 0;
    background: $color_blue url('../img/turbine.png') no-repeat left top;
    background-size: contain;
    color: $color_white;

    .content {
      flex-direction: column;
      max-width: 900px;
    }

    .subtitle {
      font-weight: bold;
      font-size: 24px;
      margin-bottom: 27px;
    }

    .text {
      line-height: 160%;
    }
  }
}

@media (max-width: $desktop) {
  .services {
    padding-top: 90px;
    margin-bottom: 90px;

    .navigation {
      margin-right: 45px;
    }

    .title {
      margin-bottom: 50px;
    }

    .item {
      min-width: 263px;

      .text {
        font-size: 14px;
      }
    }

    .block {
      .subtitle {
        font-size: 26px;
        line-height: 149.6%;
      }
    }

    .banner {
      padding-top: 40px;
      background-size: 30% auto;

      .content {
        max-width: 710px;
      }

      .subtitle {
        font-size: 22px;
      }

      .text {
      }
    }
  }
}

@media (max-width: $tablet) {
  .services {
    padding-top: 72px;
    margin-bottom: 70px;
    overflow: hidden;

    .wrapper {
      padding: 0 20px 0;
    }

    .content {
      margin-bottom: 95px;
    }

    .title {
      margin-bottom: 30px;
    }

    .navigation {
      display: none;
    }

    .block {
      .subtitle {
        font-size: 20px;
      }

      .text {
        font-size: 14px;
      }
    }

    &__slider {
      display: block;
      position: relative;
      width: 100%;
      min-width: 0;
      flex-shrink: 0;

      &_container {
        flex-shrink: 0;
      }

      .flex-viewport {
        flex-shrink: 0;
        min-height: 520px !important;
        width: 660px !important;
        overflow: visible !important;
        // padding-left: calc((100% - 660px)/2);
        // padding-right: calc((100% - 660px)/2);
      }

      &_item {
        display: flex !important;
        flex-shrink: 0;
        min-height: 320px;
        width: 660px !important;
        margin-right: 30px;
        padding: 0;
      }

      .flex-control-nav {
        bottom: -40px;
      }
    }

    .banner {
      padding-top: 30px;
      padding-right: 50px;
      background-size: 37% auto;
      background-position: -25px 0;

      .content {
        max-width: 420px;
        margin-bottom: 45px;
      }

      .subtitle {
        font-size: 18px;
      }

      .text {
        font-size: 12px;
      }
    }
  }
}

@media (max-width: $mobile) {
  .services {
    padding-top: 50px;
    margin-bottom: 52px;

    .wrapper {
      padding: 0 10px 0 14px;
    }

    .content {
      margin-bottom: 80px;
    }

    .title {
      margin-bottom: 20px;
    }

    .block {
      overflow: hidden;
      .subtitle {
        font-size: 16px;
        margin-bottom: 8px;
      }

      .image {
        height: 145px;
        width: auto;
        margin-bottom: 10px;
      }

      .text {
        font-size: 12px;
        margin-bottom: 35px;
      }
    }

    &__slider {
      .flex-viewport {
        min-height: 400px !important;
        width: 276px !important;
      }

      &_item {
        min-height: 320px;
        width: 276px !important;
      }
    }

    .banner {
      padding: 170px 23px 0;
      background-size: 250px auto;
      background-position: calc(50% - 15px) 0;

      .content {
        // max-width: 420px;
        max-width: 100%;
        margin-bottom: 45px;
      }

      .subtitle {
        font-size: 16px;
        line-height: 149.6%;
        margin-bottom: 17px;
      }

      .text {
        font-size: 12px;
      }
    }
  }
}
