.experience {
  margin-bottom: 137px;

  .title {
    margin-bottom: 75px;
  }

  &__flipping {
    @include center;
    position: absolute;
    bottom: 1px;
    right: 0;

    .flex-direction-nav a {
      top: -60px;
      width: 60px;
      height: 60px;
      border: 1px solid rgba(0, 0, 0, 0.5);
      opacity: 0.5;

      &:hover {
        opacity: 1;
      }
    }
  }

  &__slider {
    position: relative;
    width: 100%;
    min-width: 0;
    flex-shrink: 0;
    margin-bottom: 90px;

    &_container {
      width: 100% !important;
      align-items: center;
      flex-shrink: 0;
    }

    .flex-viewport {
      flex-shrink: 0;
    }

    &_item {
      display: flex !important;
      flex-shrink: 0;
      width: 1400px;
      margin: 0;
      padding: 0;
      background-color: $color_white;
      color: $color_black;

      .primarily {
        display: flex;
        flex-shrink: 0;
        width: 100%;
      }

      .image_wrapper {
        width: 540px;
        margin-right: 54px;
        flex-shrink: 0;
        overflow: hidden;
      }

      .image {
        height: 100%;
      }

      .container {
        flex-direction: column;
        justify-content: space-between;
      }

      .subtitle {
        font-weight: bold;
        font-size: 28px;
        line-height: 149.6%;
        opacity: 0.9;
        margin-bottom: 24px;
      }

      .button_bg_blue,
      .button_stroke_blue {
        padding: 0 25px 0;
      }

      .links {
        display: flex;
        margin-bottom: 33px;

        .button_stroke_blue {
          align-self: flex-start;
          min-height: 38px;
          padding: 10px 15px;
          margin-right: 15px;
          font-weight: normal;
          font-size: 12px;
          line-height: 150.9%;
          text-transform: none;
          color: $color_black;
          border-radius: 1px;

          &:hover {
            color: $color_white;
          }
        }
      }

      .list {
        flex-direction: column;
        margin-bottom: 37px;
      }

      .item {
        position: relative;
        line-height: 160%;
        padding-left: 36px;

        &::before {
          content: '';
          position: absolute;
          top: 5px;
          left: 0;
          width: 8px;
          height: 8px;
          background-color: $color_blue;
          border: 4px solid #d3e6ff;
        }

        &:not(:last-child) {
          margin-bottom: 30px;
        }
      }

      .bottom {
        display: flex;

        .button_bg_blue {
          margin-right: 40px;
        }

        .button_stroke_blue {
          border-width: 1px;
        }
      }

      .somersault {
        display: none;
      }
    }
  }

  .customers {
    display: flex;
    width: 100%;
    border: 1px solid rgba(0, 0, 0, 0.1);
    background-color: rgba(0, 0, 0, 0.1);

    .container {
      @include center;
      min-height: 155px;
      width: 20%;
      background-color: $color_white;


      &:not(:last-child) {
        border-right: 1px solid rgba(0, 0, 0, 0.1);
      }
    }

    &__slider {

      &_container {
        display: flex;
        width: 100%;
      }
    }
  }
}

@media (max-width: $desktop) {
  .experience {
    margin-bottom: 90px;

    .title {
      margin-bottom: 50px;
    }

    &__flipping {

    }

    &__slider {
      margin-bottom: 60px;

      &_item {
        width: 1140px;
        font-size: 14px;
        line-height: 160%;

        .bottom {
          .button_bg_blue {
            margin-right: 15px;
          }
        }

        .image_wrapper {
          width: 460px;
          margin-right: 40px;
        }

        .subtitle {
          font-size: 26px;
          margin-bottom: 13px;
        }

        .links {
          margin-bottom: 20px;
        }

        .item {
          &:not(:last-child) {
            margin-bottom: 16px;
          }
        }
      }
    }

    .customers {

      .container {
        min-height: 125px;

        &:nth-child(1) {
          .image {
            width: 98px;
            height: 48px;
          }
        }

        &:nth-child(2) {
          .image {
            width: 110px;
            height: 60px;
          }
        }

        &:nth-child(3) {
          .image {
            width: 90px;
            height: 63px;
          }
        }

        &:nth-child(4) {
          .image {
            width: 110px;
            height: 82px;
          }
        }

        &:nth-child(5) {
          .image {
            width: 105px;
            height: 55px;
          }
        }
      }
    }
  }
}

@media (max-width: $tablet) {
  .experience {
    overflow: hidden;
    margin-bottom: 70px;

    .wrapper {
      padding: 0 20px 0;
    }

    .title {
      margin-bottom: 30px;
    }

    &__flipping {

      .flex-direction-nav a {
        top: -50px;
        width: 50px;
        height: 50px;

        &.flex-prev {
          left: -115px;
        }
      }
    }

    &__slider {
      margin-bottom: 40px;

      &_item {
        flex-direction: column;
        width: 720px;
        font-size: 12px;
        line-height: 176%;

        .image_wrapper {
          width: 280px;
          height: 346px;
          margin-right: 30px;
        }

        .subtitle {
          font-size: 20px;
          margin-bottom: 10px;
        }

        .links {
          margin-bottom: 20px;

          .button_stroke_blue {
            padding: 0 10px;
            min-height: 28px;
            font-size: 12px;
          }
        }

        .item {
          &:not(:last-child) {
            margin-bottom: 16px;
          }
        }

        .bottom {
          display: none;
        }

        .primarily {
          margin-bottom: 30px;
        }

        .somersault {
          display: flex;

          .button_bg_blue {
            margin-right: 20px;
          }
        }
      }
    }

    .customers {
      border: none;
      background-color: transparent;

      &__slider {

        .flex-viewport {
          overflow: visible !important;
          padding-left: 0;
          width: 228px !important;
          height: 126px !important;
        }

        &_item {
          display: flex !important;
          width: 228px !important;
          border: 1px solid rgba(0, 0, 0, 0.1);
          margin-right: -1px;
        }
      }
    }
  }
}

@media (max-width: $mobile) {
  .experience {

    .wrapper {
      padding: 0 10px 0 14px;
    }

    &__slider {
      margin-bottom: 70px;

      .flex-viewport {
        width: 276px !important;
        flex-shrink: 0;
        overflow: visible !important;
      }

      &_container {
        align-items: flex-start;
      }

      &_item {
        flex-direction: column;
        width: 276px !important;
        font-size: 12px;
        line-height: 176%;

        &:not(:first-child) {
          margin: 0 5px 0;
        }

        &:first-child {
          margin-right: 5px;
        }

        .container {
          min-height: 460px;
        }

        .list {
          margin-bottom: 30px;
        }

        .image_wrapper {
          width: 280px;
          height: 346px;
          margin-right: 0;
          margin-bottom: 15px;
        }

        .subtitle {
          font-size: 16px;
          margin-bottom: 10px;
        }

        .links {
          flex-direction: column;
          margin-bottom: 20px;

          .button_stroke_blue {
            padding: 0 10px;
            min-height: 28px;
            font-size: 12px;
            margin-bottom: 6px;
          }
        }

        .item {
          &:not(:last-child) {
            margin-bottom: 16px;
          }
        }

        .bottom {
          display: none;
        }

        .primarily {
          flex-direction: column;
          margin-bottom: 0;
        }

        .somersault {
          display: flex;
          flex-direction: column;

          .button_bg_blue {
            margin-right: 0;
            margin-bottom: 30px;
          }

          .button_stroke_blue {
            padding: 0;
          }
        }
      }

      .flex-control-nav {
        bottom: -35px;
      }
    }

    .customers {
      border: none;
      background-color: transparent;

      .container {
          min-height: 89px;
      }

      &__slider {

        .flex-viewport {
          overflow: visible !important;
          padding-left: 0;
          width: 160px !important;
          height: 89px !important;
        }

        &_item {
          display: flex !important;
          width: 160px !important;
          border: 1px solid rgba(0, 0, 0, 0.1);
          margin-right: -1px;
        }
      }
    }
  }
}
