.contacts {
  margin-bottom: 120px;

  .wrapper {
    flex-direction: row;
  }

  .content {
    display: flex;
    flex-direction: column;
    margin-right: 75px;

    .link {
      font-weight: bold;
      font-size: 18px;
      line-height: 197.69%;
      text-decoration: underline;
      color: $color_blue;
      transition: .5s;

      &:hover {
        color: #4394FF;
      }
    }
  }

  .title {
    margin-bottom: 64px;
  }

  .form {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    max-width: 515px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    background-color: $color_blue;
    color: $color_white;
    padding: 40px 58px 60px;
    margin-top: 8px;

    &__title {
      font-weight: bold;
      font-size: 28px;
      line-height: 149.6%;
      opacity: 0.9;
      margin-bottom: 36px;
    }

    .input {
      &:not(:last-child) {
        margin-bottom: 20px;
      }

      &:nth-child(4) {
        margin-bottom: 40px;
      }
    }

    .button_stroke_white {
      min-height: 70px;
      margin-bottom: 30px;
    }
  }

  .consent {
    display: flex;
    font-size: 12px;
    line-height: 164.69%;

    .link {
      color: $color_white;
      text-decoration-line: underline;
    }
  }

  .title {
    margin-bottom: 64px;
  }

  .persona {
    display: flex;
    align-items: center;
    margin-bottom: 25px;

    .image {
      margin-right: 22px;
    }

    .name {
      font-weight: bold;
      font-size: 20px;
      line-height: 149.6%;
    }

    .position {
      font-size: 14px;
      line-height: 197.69%;
    }
  }

  .list {
    flex-direction: column;
    list-style-position: inside;
    margin-bottom: 36px;
  }

  .item {
    font-size:16px;
    line-height: 160%;
    padding-left: 0;
    text-indent: 10px;

    &:not(:last-child) {
      margin-bottom: 18px;
    }

    .text {
      position: relative;

      &::before {
        content: '';
        position: absolute;
        top: 8px;
        left: -10px;
        left: 0;
        width: 0;
        height: 0;
        border-radius: 50%;
        border: 1px solid $color_black;
      }
    }
  }
}

@media (max-width: $desktop) {
  .contacts {
    margin-bottom: 60px;

    .title {
      margin-bottom: 35px;
    }

    .content {
      .link {
        font-size: 16px;
        margin-bottom: 7px;
      }
    }

    .list {
      margin-bottom: 22px;
    }

    .persona {

      .name {
        font-size: 18px;
      }

      .position {
        font-size: 12px;
      }
    }

    .item {

      &:not(:last-child) {
        margin-bottom: 12px;
      }

      .text {
        font-size: 14px;
        line-height: 160%;
      }
    }

    .form {
      max-width: 380px;
      padding: 30px 25px 40px;
      margin-top: 15px;

      &__title {
        font-size: 26px;
        margin-bottom: 25px;
      }

      .input {
        min-height: 60px;
        font-size: 16px;
        padding: 0 22px;

        &:nth-child(4) {
          margin-bottom: 25px;
        }
      }

      .button_stroke_white {
        min-height: 60px;
        margin-bottom: 22px;
      }
    }
  }
}

@media (max-width: $tablet) {
  .contacts {
    margin-bottom: 47px;

    .wrapper {
      flex-direction: column;
      padding: 0 20px 0;
    }

    .title {
      margin-bottom: 30px;
    }

    .content {
      margin-right: 0;
      margin-bottom: 45px;
    }

    .item {

      &:not(:last-child) {
        margin-bottom: 5px;
      }

      .text {
        font-size: 12px;
        line-height: 167%;

        &::before {
          top: 6px;
          left: -10px;
        }
      }
    }

    .persona {
      margin-bottom: 16px;
    }

    .form {
      flex-direction: row;
      flex-wrap: wrap;
      max-width: 100%;
      width: 100%;
      margin-top: 0;
      padding: 24px 28px 40px;

      &__title {
        width: 100%;
        font-size: 20px;
        margin-bottom: 20px;
      }

      .input {
        width: 100%;
        min-height: 50px;
        font-size: 14px;

        &:nth-child(2) {
          width: 317px;
          margin-right: auto;
        }

        &:nth-child(3) {
          width: 317px;
        }
      }

      .button_stroke_white {
        width: 100%;
        min-height: 50px;
        font-size: 12px;
      }
    }
  }
}

@media (max-width: $mobile) {
  .contacts {
    margin-bottom: 40px;

    .wrapper {
      padding: 0 10px 0 14px;
    }

    .form {
      flex-direction: row;
      flex-wrap: wrap;
      max-width: 100%;
      width: 100%;
      margin-top: 0;
      padding: 28px 23px 40px;

      &__title {
        width: 100%;
        font-size: 16px;
        margin-bottom: 20px;
      }

      .input {
        width: 100%;
        min-height: 38px;
        font-size: 12px;

        &:not(:last-child) {
          margin-bottom: 10px;
        }

        &:nth-child(2) {
          width: 317px;
          margin-right: auto;
        }

        &:nth-child(3) {
          width: 317px;
        }
      }

      .button_stroke_white {
        width: 100%;
        min-height: 38px;
        font-size: 12px;
      }
    }
  }
}
