.timeline {
  margin-top: -426px;
  margin-bottom: 130px;
  color: $color_white;

  .title {
    color: $color_blue;
    margin-bottom: 30px;
  }

  .text {
    width: 100%;
    font-size: 18px;
    line-height: 165.69%;
    margin-bottom: 60px;
  }

  &__flipping {
    @include center;
    position: absolute;
    bottom: 0;
    left: 154px;
  }

  &__slider {
    position: relative;
    width: 100%;
    min-width: 0;
    flex-shrink: 0;


    &_container {
      width: 100% !important;
      align-items: center;
      flex-shrink: 0;
    }

    .flex-viewport {
      flex-shrink: 0;
    }

    &_item {
      display: flex !important;
      flex-shrink: 0;
      width: 1400px;
      margin: 0;
      padding: 0;
      background-color: $color_blue;
      color: $color_white;

      .container {
        flex-direction: column;
        margin: 60px 70px 70px;

        &:first-child {
          width: 43%;
        }

        &:last-child {
          width: auto;
        }
      }

      .subtitle {
        font-weight: bold;
        font-size: 28px;
        line-height: 149.6%;
        margin-bottom: 30px;
      }

      .links {
        display: flex;
      }

      .button_stroke_white {
        align-self: flex-start;
        min-height: 38px;
        padding: 10px 15px;
        margin-right: 15px;
        font-weight: normal;
        font-size: 12px;
        line-height: 150.9%;
        text-transform: none;
      }

      .indicators {
        &:not(:last-child) {
          margin-bottom: 33px;
        }
      }

      .number {
        @include font_Furore;
        font-size: 40px;
        line-height: 136.6%;
        letter-spacing: 0.05em;
        margin-bottom: 10px;
      }

      .name {
        font-weight: 500;
        font-size: 16px;
        line-height: 197.69%;
        opacity: 0.7;
      }
    }
  }
}

@media (max-width: $desktop) {
  .timeline {
    margin-top: -360px;
    margin-bottom: 90px;

    .title {
      margin-bottom: 20px;
    }

    .text {
      font-size: 16px;
    }

    &__flipping {
      left: 130px;
    }

    &__slider {

      &_item {
        min-height: 420px;
        width: 1140px;

        .container {
          flex-direction: column;
          margin: 64px 50px 70px;
        }

        .subtitle {
          font-size: 26px;
          margin-bottom: 45px;
        }

        .number {
          font-size: 36px;
          line-height: 100%;
          margin-bottom: 7px;
        }

        .name {
          font-size: 14px;
        }
      }
    }
  }
}

@media (max-width: $tablet) {
  .timeline {
    margin-bottom: 72px;

    .wrapper {
      padding: 0 20px 0;
    }

    .title {
      margin-bottom: 20px;
    }

    .text {
      font-size: 14px;
      margin-bottom: 45px;
    }

    &__flipping {
      left: 100px;
    }

    &__slider {

      &_item {
        min-height: 395px;
        width: 720px;
        flex-direction: column;

        .container {
          flex-direction: column;
          margin: 43px 33px 0;

          &:first-child {
            width: 100%;
            margin-bottom: 15px;
          }

          &:last-child {
            width: 100%;
            flex-direction: row;
          }
        }

        .subtitle {
          font-size: 20px;
          margin-bottom: 28px;
        }

        .button_stroke_white {
          min-height: 28px;
          font-size: 12px;
          padding: 0 10px 0;
        }

        .indicators {

          &:nth-child(1) {
            width: 30%;
          }

          &:nth-child(2) {
            width: 28%;
          }

          &:nth-child(3) {
            width: auto;
          }

          &:not(:last-child) {
            margin-bottom: 0;
          }
        }

        .number {
          font-size: 30px;
          line-height: 100%;
          margin-bottom: 12px;
        }

        .name {
          font-size: 12px;
          line-height: 120%;
        }
      }
    }
  }
}

@media (max-width: $mobile) {
  .timeline {
    margin-top: -495px;
    margin-bottom: 63px;

    .wrapper {
      padding: 0 10px 0 14px;
    }

    .title {
      margin-bottom: 15px;
    }

    .text {
      font-size: 14px;
      margin-bottom: 32px;
    }

    &__slider {

      &_item {
        min-height: 395px;
        width: 290px;
        flex-direction: column;

        .container {
          flex-direction: column;
          margin: 30px 25px 0;

          &:first-child {
            width: auto;
            margin-bottom: 25px;
          }

          &:last-child {
            width: auto;
            flex-direction: column;
            margin-top: 0;
            margin-bottom: 85px;
          }
        }

        .subtitle {
          font-size: 16px;
          margin-bottom: 15px;
        }

        .links {
          display: flex;
          flex-direction: column;
        }

        .button_stroke_white {
          margin-bottom: 10px;
        }

        .indicators {

          &:nth-child(1) {
            width: auto;
          }

          &:nth-child(2) {
            width: auto;
          }

          &:nth-child(3) {
            width: auto;
          }

          &:not(:last-child) {
            margin-bottom: 24px;
          }
        }

        .number {
          font-size: 30px;
          line-height: 100%;
          margin-bottom: 12px;
        }

        .name {
          font-size: 12px;
          line-height: 120%;
        }
      }
    }
  }
}
